.support {

    margin-bottom: 21.8rem;

    &__title {
        font-size: 4.5rem;
        font-weight: 700;
        line-height: 1.2;

        margin-bottom: 3.5rem;
    }

    &__content {
        width: 157.1rem;
        height: 83.7rem;

        display: flex;
        flex-wrap: wrap;


        @media (max-width: 48em) {
            width: 75rem;
         }
    }

    &__img {
        object-fit: cover;

        @media (max-width: 48em) {
            display:none;
         }
    }

    &__img-mob {
        display: none;

        @media (max-width: 48em) {
           display: block;
           object-fit: contain;

        }
    }

    &__num {
        font-size: 6.0rem;
        font-weight: 500;
        line-height: 1.2;

        border-bottom: 1px dashed red;
        width: 39rem;

        padding-bottom: 5.1rem;
        position: relative;

        &::after {
            content: "";
            width: 0.865rem;
            height: 0.865rem;
            background-color: #FC281F;
            border-radius: 50%;

            position: absolute;
            bottom: -5%;
            left: 0%;
        }


    }

    &__box {
        width: 35rem;
        padding: 2rem 1.5rem 1.5rem 1.5rem;
    }

    &__box-inner {            
        border-right: 1px dashed red;
        width: 35rem;
        
    }


    &__box-img-wrap {
        width: 8rem;
        height: 7.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__box-img {
        width: 8rem;
        height: 7.5rem;
        object-fit: contain;
    }

    &__box-title {
        font-size: 1.95rem;
        font-weight: 400;
        line-height: 1.4;
    }

    &__box-subtitle {
        font-size: 1.8rem;
        font-weight: 300;
        line-height: 1.4;
    }
}